export enum ResubmitEffect {
    /**
     * 驳回的节点通过后，按顺序流转
     */
    sequence = 'sequence',
    /**
     * 驳回后重新提交至我
     * 1.抢占节点：只给驳回人创建任务
     * 2.会签节点：驳回人+未审批人收到新任务，已通过人员自动通过
     */
    backToMe = 'backToMe',
    /**
     * 驳回的节点通过后返回本节点
     * 1.抢占节点：1).再次进入节点时，办理人
     * 2.会签节点：不读取参与者重新进入此节点历史办理人作为审批人配置
     */
    backToCurrentNode = 'backToCurrentNode',
}