import { ActionScope } from "../ActionScope";
import { OrganizationLevel } from "../OrganizationLevel";

export class ParticipantSchemeForDesigner {
    constructor() {
        this.performers = [];
        this.variablePerformers = []
        this.components = [];
        this.reportingLines = [];
        this.activityExecutors = [];
        this.matrixParticipants = [];
        this.organizationParticipants = new Array();
        this.options = {
            ExclusionPerformers: [],
            NoPerformerStrategy: '',
        }
    }
    performers: ParticipantItem[];
    variablePerformers: any[];
    components: any[];
    reportingLines: any[];
    activityExecutors: any[];
    matrixParticipants: any[];
    organizationParticipants: Array<OrganizationParticipant>;
    options: {
        ExclusionPerformers: Array<string>,
        NoPerformerStrategy: string,
        HistoricFirst?: boolean,
        automaticPerformers?: Array<string>
    };
    isAllScope: boolean = false;
}

export class ParticipantItem {
    id: string;
    performerId: string;
    performerName: string;
    positionType?: string;
    orgId: string;
    orgName: string;
    orgType?: string;
    type: string;
    condition?: string;
    tag?: string;
    varValue?: string;
    varType?: string;
    relation?: string;
}

export class OrganizationParticipant {
    id: string;
    name: string;
    actionScope: ActionScope;
    organizationLevel: OrganizationLevel;
    organizationId: string;
    organizationCode: string;
    organizationName: string;
    includeChildren: boolean;
    parentsUntil: boolean;
    tag: string;

    condition: string;
}
