import { BpmnModel, BpmnModelHelper, DataObject, ExtensionElement } from "@edp-pmf/bpmn-model";
import { VariableSetting } from "./VariableSetting";

export class ValuedDataObject extends DataObject {
    static Clr_Type_ID: string = "wf.ValuedDataObject";
    readonly ClrTypeID: string = ValuedDataObject.Clr_Type_ID;
    protected static MEXTAG: string = 'wf.ValuedDataObject';

    variableSetting: VariableSetting;

    constructor(model: BpmnModel) {
        super(model);
    }

    protected GetExtensionElements4ConvertTo(): ExtensionElement[] {
        let list = super.GetExtensionElements4ConvertTo();
        if (this.variableSetting != null) {
            this.variableSetting.MexTag = ValuedDataObject.MEXTAG;
            list.push(this.variableSetting);
        }
        return list;
    }
    protected SetExtensionElements4LoadFrom(extensionElements: ExtensionElement[]) {
        super.SetExtensionElements4LoadFrom(extensionElements);
        for (let extensionElement of extensionElements) {
            if (extensionElement.MexTag !== ValuedDataObject.MEXTAG)
                continue;
            switch (extensionElement.ClrTypeID) {
                case VariableSetting.Clr_Type_ID:
                    this.variableSetting = (extensionElement as VariableSetting);
                    break;
                default:
                    break;
            }
        }
    }
    NewInstance(): ValuedDataObject {
        return new ValuedDataObject(this.Model);
    }
}
BpmnModelHelper.RegisterElementType(new ValuedDataObject(new BpmnModel()));