import { BpmnModel, BpmnModelHelper, MexElement, DataType } from "@edp-pmf/bpmn-model";
import { ParameterMode } from "./ParameterMode";

export class FormalParameter extends MexElement {
    private index: number;

    constructor(model: BpmnModel, id?: string, code?: string, name?: string, desc?: string, index?: number, datatype?: DataType, mode?: ParameterMode) {
        super(model);
        this.Id = id || '';
        this.Code = code || '';
        this.Name = name || '';
        this.Description = desc || '';
        this.index = index || 0;
        this.DataType = datatype || new DataType(model);
        this.Mode = mode || ParameterMode.IN;
    }

    Id: string;
    Code: string;
    Name: string;
    Description: string;

    get Index(): number {
        return this.index;
    }
    set Index(index: number) {
        this.index = index;
    }

    DataType: DataType;
    Mode: ParameterMode;

    protected ResetBeforeLoadFrom() {
        super.ResetBeforeLoadFrom();
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Id", this.Id);
        BpmnModelHelper.SetAttributeValue(obj, "Code", this.Code);
        BpmnModelHelper.SetAttributeValue(obj, "Name", this.Name);
        BpmnModelHelper.SetAttributeValue(obj, "Description", this.Description);
        BpmnModelHelper.SetAttributeValue(obj, "index", this.index);
        BpmnModelHelper.SetAttributeValue(obj, "DataType", this.DataType.ConvertToJsonObject());
        BpmnModelHelper.SetAttributeValue(obj, "Mode", this.Mode);

        return obj;
    }
    LoadFromJsonObject(obj: any) {
        super.LoadFromJsonObject(obj);

        this.Id = obj.Id;
        this.Name = obj.Name;
        this.index = obj.index;
        this.Description = obj.Description;
        this.Code = obj.Code;

        let type: DataType = new DataType(this.Model);
        type.LoadFromJsonObject(obj.DataType);
        this.DataType = type;

        this.Mode = (obj.Mode as ParameterMode);
    }

    clone(): FormalParameter {
        const f = new FormalParameter(this.Model);

        return f
    }
}