import { BpmnModel, BpmnModelHelper, HumanPerformer, ExtensionElement } from "@edp-pmf/bpmn-model";
import { Condition } from "../../extension/condition-expression/Condition";

export abstract class Participant extends HumanPerformer {
    constructor(model: BpmnModel, id?: string, name?: string) {
        super(model);
        this.Id = id;
        this.Name = name;
    }

    ClrTypeID: string = "wf.Participant";
    MexTag: string = 'wf.Participant';
    Condition: Condition;

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();
        return obj;
    }

    LoadFromJsonObject(obj: any): any {
        super.LoadFromJsonObject(obj);
    }

    protected GetExtensionElements4ConvertTo(): any {
        let list = super.GetExtensionElements4ConvertTo();
        if (this.Condition) {
            this.Condition.MexTag = this.MexTag;
            list.push(this.Condition);
        }
        return list;
    }

    protected SetExtensionElements4LoadFrom(extensionElements: ExtensionElement[]) {
        super.SetExtensionElements4LoadFrom(extensionElements);
        for (const item of extensionElements) {
            if (item instanceof Condition) {
                const condition = item as Condition;
                if (condition.MexTag === this.MexTag) {
                    this.Condition = condition;
                }
            }
        }
    }
    //public IList<ExtensionElement> GetExtensionElements() {
    //    return this.extensionElements;
    //}
}