import { BpmnModel, BpmnModelHelper, ExtensionElement, MexExtensionElement } from "@edp-pmf/bpmn-model";

export abstract class WFExtensionElement extends ExtensionElement {
    Id: string;
    Code: string;
    Name: string;
    Description: string;
    ExtendedAttributes: { [key: string]: string } = {};

    protected ResetBeforeLoadFrom() {
        super.ResetBeforeLoadFrom();
        this.ExtendedAttributes = {};
    }

    protected constructor(model: BpmnModel, id?: string, name?: string) {
        super(model);
        this.Id = id || '';
        this.Name = name || '';
    }

    ConvertToJsonObject(): any {
        let obj = super.ConvertToJsonObject();

        BpmnModelHelper.SetAttributeValue(obj, "Id", this.Id);
        BpmnModelHelper.SetAttributeValue(obj, "Code", this.Code);
        BpmnModelHelper.SetAttributeValue(obj, "Name", this.Name);
        BpmnModelHelper.SetAttributeValue(obj, "Description", this.Description);

        let extAttributeElements = [];

        for (let key in this.ExtendedAttributes) {
            let obj = {};
            BpmnModelHelper.SetAttributeValue(obj, "Name", key);
            BpmnModelHelper.SetAttributeValue(obj, "Value", this.ExtendedAttributes[key]);
            extAttributeElements.push(obj);
        }
        BpmnModelHelper.SetAttributeValue(obj, "ExtendedAttributes", extAttributeElements);

        if (this.GetMexExtensionElements4ConvertTo().length > 0) {

            let mexJsonArray = [];
            this.GetMexExtensionElements4ConvertTo().forEach((value) => {
                mexJsonArray.push(value.ConvertToJsonObject());
            });
            BpmnModelHelper.SetAttributeValue(obj, "MexExtensionElements", mexJsonArray);
        }

        return obj;
    }

    LoadFromJsonObject(obj: any) {
        super.LoadFromJsonObject(obj);

        this.Id = obj.Id;
        this.Code = obj.Code;
        this.Name = obj.Name;
        this.Description = obj.Description;

        let extAttributeElements = obj.ExtendedAttributes as Array<any>;
        if (extAttributeElements && extAttributeElements.length > 0) {
            extAttributeElements.forEach((item) => {
                this.ExtendedAttributes[item.Name] = item.Value;
            });
        }

        let mexJsonArray = obj.MexExtensionElements as Array<any>;
        if (mexJsonArray && mexJsonArray.length > 0) {
            let list = new Array<MexExtensionElement>();
            mexJsonArray.forEach((item) => {
                let mexExtElementObj = BpmnModelHelper.GetElementType(item.ClrTypeID) as MexExtensionElement;
                if (mexExtElementObj) {
                    mexExtElementObj.LoadFromJsonObject(item);
                    list.push(mexExtElementObj);
                }
            });
            this.SetMexExtensionElements4LoadFrom(list);
        }
    }

    protected GetMexExtensionElements4ConvertTo(): MexExtensionElement[] {
        return [];
    }
    protected SetMexExtensionElements4LoadFrom(list: MexExtensionElement[]) {

    }

    GetExtendedAttributes(): { [key: string]: string } {
        return this.ExtendedAttributes;
    }

}